import React, { useState, useEffect } from "react"
import CocktailDisplay from "./cocktaildisplay";
import { Button } from 'antd';
import CocktailAnimation from './cocktailanimation'


function RandomCocktail() {
    const [data, setData] = useState('');
    const [id, setId] = useState(0);
    const [isLoading, setIsLoading] = useState(true)
  
    useEffect(() => {
      if (id !== 0) {
        fetchIngredients()
        setIsLoading(true);
      }
      async function fetchIngredients() {
        try {
          const response = await fetch('https://www.thecocktaildb.com/api/json/v2/9973533/random.php');
          const apiData = await response.json();
          setData(apiData.drinks[0]);
          setIsLoading(false);
          } catch (err) {
          console.log(err);
      }
      }
    }, [id] );

    const handleClick = (e) => {
      e.preventDefault();
      setId(id + 1)
    }
    if(id === 0)
    return (
      <div>
        <h2>Shake me up a drink</h2>
        <div onClick={handleClick}>
          <CocktailAnimation props={'rocksGlass'}/>
        </div>
      </div>
    )
    if (isLoading === true)
    return (
    <div>
        <CocktailAnimation props={'rocksGlassLoading'}/>
    </div>
    )
    return (
      <div>
            <Button onClick={handleClick}>
              Something Different
            </Button>
            <CocktailDisplay data={data} />
      </div>
    
    );
  }
  
  export default RandomCocktail;